<template>
	<div class="container">
        <div class="PlayerRanking_header">
            <img @click="toReturn" :src="staticImgH+'zuojiantou.png'" alt="">
            <span>视频</span>
            <span ></span>
        </div>
        <div class="play">
            <video-player class="video-player-box ovideo" controls="controls" autoplay="autoplay" ref="videoPlayer" :options="playerOptions" :playsinline="true">
            </video-player></div>
        </div>
</template>
 
<script>
import {mapState} from 'vuex'
  import {mapMutations} from 'vuex'
  import {videoPlayer} from 'vue-video-player'
  import 'video.js/dist/video-js.css'
export default {
	name: 'living',
	data () {
		return {
			playerOptions: {
				playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
          autoplay: true, //如果true,浏览器准备好时开始回放。
          muted: false, // 默认情况下将会消除任何音频。
          loop: false, // 导致视频一结束就重新开始。
          preload: 'auto', // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
          language: 'zh-CN',
          aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
          fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
          sources: [{
          src: 'http://aibeiwang.oss-cn-beijing.aliyuncs.com/yyh/test/50527a01eb7f2a047261cb88a95630b550527a01eb7f2a047261cb88a95630b5.mp4',  // 路径
          type: 'video/mp4'  // 类型
        }, {
          src: '//path/to/video.webm',
          type: 'video/webm'
        }],
          poster: "../../static/images/test.jpg", //你的封面地址
          // width: document.documentElement.clientWidth,
          notSupportedMessage: '此视频暂无法播放，请稍后再试', //允许覆盖Video.js无法播放媒体源时显示的默认信息。
          controlBar: {
          timeDivider: true,
            durationDisplay: true,
            remainingTimeDisplay: false,
            fullscreenToggle: true  //全屏按钮
        }
			}
		}
    },
     computed: {
      ...mapState(['staticImgH', 'tokenH', 'playerStyleDetailedInfo'])
    },
	methods: {
        //事件
        toReturn(){
            this.$router.push('/')
        }
	}
}
</script>
 
<style scoped lang="stylus">
.container{
    width:100%;
    height:100%;
    >.play{
        width:100%;
        margin-top:0.6rem;
    }
}
.PlayerRanking_header{
        width:100%
        height:1.23rem;
        display :flex;
        align-items :center;
        justify-content :space-between;
        position :relative;
        padding:0 0.4rem;
        >img{
            width:0.32rem;
            height:0.56rem;
        }
        >span{
            font-size:0.48rem;
            color:rgba(0, 0, 0, 1);
        }
    }
</style>


